import React from "react"
import { useStaticQuery, graphql, Link } from "gatsby"
import Bio from "../components/bio"
import Layout from "../components/layout"

const CategoryPost = ({ location, pageContext }) => {
  const { category, subcategory } = pageContext

  const data = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title
        }
      }
      allMarkdownRemark {
        nodes {
          id
          fields {
            slug
          }
          frontmatter {
            main
            sub
            title
            date(formatString: "MMMM DD, YYYY")
            description
          }
        }
      }
    }
  `)

  const posts = data.allMarkdownRemark.nodes
  const title = data.site.siteMetadata.title

  if (!subcategory) {
    const filteredPosts = posts.filter(
      post => post.frontmatter.main === category
    )
    return (
      <Layout location={location} title={title} isCategory={true}>
        <Bio />
        {filteredPosts.map(post => (
          <li key={post.fields.slug} style={{ listStyle: `none` }}>
            <article
              className="post-list-item"
              itemScope
              itemType="http://schema.org/Article"
            >
              <header>
                <h2>
                  <Link to={post.fields.slug} itemProp="url">
                    <span itemProp="headline">{post.frontmatter.title}</span>
                  </Link>
                </h2>
                <small>{post.frontmatter.date}</small>
              </header>
              <section>
                <p
                  dangerouslySetInnerHTML={{
                    __html: post.frontmatter.description || post.excerpt,
                  }}
                  itemProp="description"
                />
              </section>
            </article>
          </li>
        ))}
      </Layout>
    )
  }

  const filteredPosts = posts.filter(
    post =>
      post.frontmatter.main === category && post.frontmatter.sub === subcategory
  )

  return (
    <Layout location={location} title={title} isCategory={true}>
      <Bio />
      {filteredPosts.map(post => (
        <li key={post.fields.slug} style={{ listStyle: `none` }}>
          <article
            className="post-list-item"
            itemScope
            itemType="http://schema.org/Article"
          >
            <header>
              <h2>
                <Link to={post.fields.slug} itemProp="url">
                  <span itemProp="headline">{post.frontmatter.title}</span>
                </Link>
              </h2>
              <small>{post.frontmatter.date}</small>
            </header>
            <section>
              <p
                dangerouslySetInnerHTML={{
                  __html: post.frontmatter.description || post.excerpt,
                }}
                itemProp="description"
              />
            </section>
          </article>
        </li>
      ))}
    </Layout>
  )
}

export default CategoryPost
